<template>
  <div id="note" class="detail">
    <note-sidebar @update:notes="arr => (notes = arr)"></note-sidebar>
    <div class="note-detail">
      <div class="note-empty" v-show="!curNote.id"></div>
      <div v-show="curNote.id" class="note-detail-ct">
        <div class="note-bar">
          <span>{{ curNote.createdAtFriendly }}</span>
          <span>{{ curNote.updatedAtFriendly }}</span>
          <span>{{ statusText }}</span>
          <span class="iconfont icon-delete" @click="onDeleteNote"></span>
          <span
            class="iconfont icon-fullscreen"
            @click="isShowPreview = !isShowPreview"
          ></span>
        </div>
        <div class="note-title">
          <input
            type="text"
            v-model="curNote.title"
            @input="onUpdateNote"
            @keydown="statusText = '...'"
            placeholder=""
          />
        </div>
        <div class="editor">
          <textarea
            v-show="!isShowPreview"
            v-model="curNote.content"
            @input="onUpdateNote"
            @keydown="statusText = '...'"
            placeholder="markdown"
          ></textarea>
          <div
            class="preview markdown-body"
            v-show="isShowPreview"
            v-html="previewContent"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Auth from "@/apis/auth";
import NoteSidebar from "@/components/NoteSidebar.vue";
// import Bus from "@/helpers/bus";
import _ from "lodash";
// import Notes from "@/apis/notes";
import MarkdownIt from "markdown-it";
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";

let md = new MarkdownIt();

export default {
  components: { NoteSidebar },
  data() {
    return {
      statusText: "",
      isShowPreview: false
    };
  },

  created() {
    this.checkLogin({ path: "/login" })
    // Auth.getInfo().then(res => {
    //   if (!res.isLogin) {
    //     this.$router.push({ path: "/login" });
    //   }
    // });
  },

  computed: {
    ...mapGetters(["notes", "curNote"]),
    previewContent() {
      return md.render(this.curNote.content || "");
    }
  },

  methods: {
    ...mapMutations(["setCurNote"]),

    ...mapActions(["updateNote", "deleteNote",'checkLogin']),
    // testcreatedcurNoteeventBus
    // test(val){
    //   this.curNote = val.find(note => note.id == this.$route.query.noteId) || {}
    // }

    onUpdateNote: _.debounce(function() {
      if(!this.curNote.id) return
      this.updateNote({
        noteId: this.curNote.id,
        title: this.curNote.title,
        content: this.curNote.content
      })
        .then(res => {
          this.statusText = "";
        })
        .catch(err => {
          this.statusText = "";
        });
    }, 300),

    onDeleteNote() {
      this.deleteNote({ noteId: this.curNote.id }).then(res => {
        // this.notes.splice(this.notes.indexOf(this.curNote), 1);
        this.$router.replace({ path: "/note" });
      });
    }
  },

  beforeRouteUpdate(to, from, next) {
    // console.log("beforeRouteUpdate");
    // console.log(to, from);
    this.setCurNote({ curNoteId: to.query.noteId });
    // curNotenotes
    // curNotevuexgettervuex!!!
    // this.curNote = this.notes.find(note => note.id == to.query.noteId) || {};
    next();
  }
};
</script>

<style scoped lang="less">
@import url(../assets/css/note-detail.less);

#note {
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex: 1;
}
</style>



